function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderCatalog() {
    var swiper = new Swiper('.catalog .swiper', {
        slidesPerView: '4',
        spaceBetween: 30,
        navigation: {
            nextEl: '.catalog .swiper-button-next',
            prevEl: '.catalog .swiper-button-prev',
        },
        pagination: {
            el: ".catalog .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            541: {
                slidesPerView: 2,
            },
            741: {
                slidesPerView: 3,
            },
            1301: {
                slidesPerView: 4,
            },
        }
    })
}

function sliderNews() {
    var swiper = new Swiper('.news .swiper', {
        slidesPerView: '3',
        spaceBetween: 30,
        navigation: {
            nextEl: '.news .swiper-button-next',
            prevEl: '.news .swiper-button-prev',
        },
        pagination: {
            el: ".news .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 2,
            },
            1101: {
                slidesPerView: 3,
            },
        }
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: 4,
        spaceBetween: 20,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "vertical",
        breakpoints: {
            320: {
                slidesPerView: 4,
                spaceBetween: 10,
            },
            1401: {
                slidesPerView: 4,
                spaceBetween: 20,
            },
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        pagination: {
            el: ".gallery .swiper-pagination",
            clickable: true,
        },
    });
}

function sliderProduct() {
    var swiper = new Swiper('.product-info .swiper', {
        slidesPerView: '1',
        spaceBetween: 30,
        pagination: {
            el: ".product-info .swiper-pagination",
            clickable: true,
        },
    })
}


$(document).ready(function() {

    sliderMain();
    sliderCatalog();
    sliderNews();
    sliderProduct()

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-bottom').toggleClass('header-bottom--active')
        $('body').toggleClass('fixed-body')
    })

    // // Ð¡atalog-menu
    $(".header-bottom__item").hover(onIn, onOut);
    $(".header-bottom__item .header-bottom__item-show").click(function() {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    //
    $('.header-bottom__list li').hover(function () {
        $(this).siblings('.header-bottom__list li').toggleClass('opacity')
    })

    //
    $('.header-bottom__item-link').hover(function () {
        $(this).siblings('.header-bottom__item-link').toggleClass('opacity')
    })

    // Filter
    $('.catalog-main__filter-btn').click(function () {
        $('.catalog-filter').addClass('catalog-filter--active')
        $('.catalog-filter__bg').addClass('catalog-filter__bg--active')
    })

    let catalogFilter = $('.catalog-filter')

    $(document).mouseup(function (e) {
        if ( !catalogFilter.is(e.target) && catalogFilter.has(e.target).length === 0

        ) {
            catalogFilter.removeClass('catalog-filter--active')
            $('.catalog-filter__bg').removeClass('catalog-filter__bg--active')
        }
    })

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });


    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs-content").removeClass("tabs-content--active")
        $(this).parents(".tabs").find(`.tabs-content[data-content-path="${path}"]`).addClass("tabs-content--active")
    })

    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()

    // Range
    const rangeSlider = document.getElementById('range-slider');

    if (rangeSlider) {
        noUiSlider.create(rangeSlider, {
            start: [0, 10000],
            connect: true,
            step: 1,
            range: {
                'min': [0],
                'max': [10000]
            }
        });

        const input0 = document.getElementById('input-0');
        const input1 = document.getElementById('input-1');
        const inputs = [input0, input1];

        rangeSlider.noUiSlider.on('update', function(values, handle){
            inputs[handle].value = Math.round(values[handle]);
        });

        const setRangeSlider = (i, value) => {
            let arr = [null, null];
            arr[i] = value;

            console.log(arr);

            rangeSlider.noUiSlider.set(arr);
        };

        inputs.forEach((el, index) => {
            el.addEventListener('change', (e) => {
                console.log(index);
                setRangeSlider(index, e.currentTarget.value);
            });
        });
    }

    gallery();

})

function onIn() {
    if (window.innerWidth > 1100) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 1100) {
        $(this).removeClass("header-bottom__item--active")
    }
}